import s from './Card.module.css';
import uax from '../../uax/demo'

function Card({ title, label, buttonText, client, from, to, value, children }) {

  async function demoTransfer(event) {
    event.preventDefault();

    await uax.doTransfer(client, from, to, value)
  }

  const defaultUAXValue = 0;

  return (
    <section className={s.card}>
      <h2 className={s.title}>{title}</h2>
      <form className={s.block}>
        <label className={s.label}>Recipient</label>
        <input
          className={s.input}
          type="text"
          placeholder="Enter recipient 0:address"
          defaultValue={to}
        />
        <label className={s.label}>{label}</label>
        <input className={s.input} type="text" placeholder="Enter amount" />
        {children}
        <button className={s.button} type="submit" onClick={demoTransfer}>
          {buttonText}
        </button>
      </form>
    </section>
  );
}

export default Card;
